& invi?.ceraEnd\"\r\n                    class=\"guest-dash-info\"\r\n                >\r\n                    <img src=\"/assets/guestDash/calender.svg\" alt=\"\" />\r\n                    <span\r\n                        ><b>Ceremonies:</b>\r\n                        <!-- {{ invi.ceraStart | moment(\"dddd, MMMM D, YYYY\") }} to\r\n                        {{ invi.ceraEnd | moment(\"dddd, MMMM D, YYYY\") }} -->\r\n                        {{ invi.startDate | moment(\"dddd, MMMM D, YYYY\") }}\r\n                    </span>\r\n                </div>\r\n                <div v-if=\"invi.venues.length > 0\" class=\"guest-dash-info\">\r\n                    <img src=\"/assets/guestDash/location.svg\" alt=\"\" />\r\n                    <span v-for=\"e in invi.venues\" :key=\"e.name\"\r\n                        >{{ e.name }}, {{ e.location.name }},\r\n                        {{ e.location.state }}\r\n                    </span>\r\n                </div>\r\n            </div>\r\n        </a>\r\n        <div class=\"guest-dash-card-btns\">\r\n            <button @click=\"$emit('view', invi)\" type=\"button\">\r\n                <img src=\"/assets/guestDash/eye.svg\" alt=\"\" />\r\n            </button>\r\n            <button @click=\"$emit('accept', invi)\" type=\"button\">\r\n                <img src=\"/assets/guestDash/check.svg\" alt=\"\" />\r\n            </button>\r\n            <button @click=\"$emit('decline', invi)\" type=\"button\">\r\n                <img src=\"/assets/guestDash/x.svg\" alt=\"\" />\r\n            </button>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n    props: [\"invi\", \"user\", \"active\"],\r\n    computed: {\r\n        setClass() {\r\n            if (this.active == \"accepted\") {\r\n                return \"green\";\r\n            }\r\n            if (this.active == \"declined\") {\r\n                return \"red\";\r\n            }\r\n            if (this.active == \"pending\") {\r\n                return \"yellow\";\r\n            }\r\n        },\r\n    },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n/* card csss start */\r\n.red {\r\n    background: #c94b33 !important;\r\n}\r\n.yellow {\r\n    background: #e26b00 !important;\r\n}\r\n.green {\r\n    background: #23871d !important;\r\n}\r\n.guest-dash-card {\r\n    background: #ffffff;\r\n    border: 7.91317px solid #f3f3f3;\r\n    border-radius: 6.37724px;\r\n    padding: 3px;\r\n    max-width: 213px;\r\n    width: 100%;\r\n    margin-right: 10px;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: space-between;\r\n}\r\n.guest-dash-card-content {\r\n    padding: 8px;\r\n    background: #ffffff;\r\n    display: flex;\r\n    text-decoration: none;\r\n    flex-direction: column;\r\n}\r\n.img-cont {\r\n    position: relative;\r\n    padding-top: 80%;\r\n    margin-bottom: 15px;\r\n}\r\n.guest-dash-card-content > .img-cont > img {\r\n    width: 100%;\r\n    height: 100%;\r\n    object-fit: cover;\r\n\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n}\r\n.guest-dash-card-head {\r\n    display: flex;\r\n    align-items: center;\r\n}\r\n.guest-dash-card-head > span:nth-child(1) {\r\n    background: #461952;\r\n    border-radius: 6.37724px;\r\n    padding: 7px 8px;\r\n    display: inline-block;\r\n    text-align: center;\r\n    vertical-align: center;\r\n    font-family: \"Poppins\";\r\n    font-style: normal;\r\n    font-weight: 500;\r\n    font-size: 13.1462px;\r\n    line-height: 20px;\r\n    margin-right: 10px;\r\n    color: #ffffff;\r\n}\r\n.guest-dash-card-head > span:nth-child(1) > span {\r\n    font-weight: 300;\r\n    font-size: 9.14521px;\r\n    line-height: 14px;\r\n    display: block;\r\n}\r\n.guest-dash-card-head > span:nth-child(2) {\r\n    font-family: \"Poppins\";\r\n    font-style: normal;\r\n    font-weight: 500;\r\n    font-size: 13.1462px;\r\n    line-height: 20px;\r\n    color: #000000;\r\n}\r\n.guest-dash-info {\r\n    display: flex;\r\n    align-items: center;\r\n    margin: 5px 0;\r\n}\r\n.guest-dash-info > img {\r\n    margin-right: 10px;\r\n}\r\n.guest-dash-info > span {\r\n    font-family: \"Poppins\";\r\n    font-style: normal;\r\n    font-weight: 400;\r\n    font-size: 9.81904px;\r\n    line-height: 15px;\r\n    color: #99a0af;\r\n}\r\n.guest-dash-card-btns {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    margin-top: 5px;\r\n    border-top: 5.91317px solid #f3f3f3;\r\n}\r\n.guest-dash-card-btns > button {\r\n    border: none;\r\n    outline: none;\r\n    background: #ffffff;\r\n    border-radius: 2.5509px;\r\n    width: 66px;\r\n    height: 43px;\r\n    display: block;\r\n}\r\n\r\n/* card css end */\r\n</style>\r\n"],"sourceRoot":""}]);
