                                    <img
                                        src="/assets/guestDash/Ellipse.svg"
                                        alt=""
                                    />
                                    <span
                                        >{{ item.name }}
                                        <span
                                            >- {{ item.invitation.brideName }} &
                                            {{
                                                item.invitation.groomName
                                            }}</span
                                        ></span
